import React, { useState, useEffect } from "react";
import {
  faEdit,
  faEllipsisH,
  faEye,
  faSearch,
  faPlus,
} from "@fortawesome/free-solid-svg-icons";
import {
  Col,
  Nav,
  Card,
  Table,
  Pagination,
  Breadcrumb,
  Form,
  Button,
  Row,
  ButtonGroup,
  InputGroup,
  Dropdown,
  Modal,Badge
} from "@themesberg/react-bootstrap";
import jwt_decode from "jwt-decode";
import moment from "moment-timezone";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useHistory } from "react-router-dom";
import axios from "axios";
import {
  CDropdown,
  CDropdownItem,
  CDropdownMenu,
  CDropdownToggle,
} from "@coreui/bootstrap-react";
import Swal from "sweetalert2";

const token = JSON.parse(localStorage.getItem("token"));
let decoded = null;
if (token !== null) decoded = jwt_decode(token);

export default function AllProfiles() {
  const history = useHistory();
  const [show, setShow] = useState(false);
  const [data, setData] = useState([]);
  const [ticketId, setTicketId] = useState();

  useEffect(() => {
    const fetchData = async () => {
      try {
        const { data: response } = await axios.get(
          "http://localhost:3000/api/v1/profile/alltickets"
        );
        setData(response);
      } catch (error) {
        console.error(error.message);
      }
    };

    fetchData();
  }, []);
  const [formData, setFormData] = useState({
    sender:decoded.userId,
    subject: "",
    message:"",
  });

  const [formData2, setFormData2] = useState({
    status:"",
  });
  const config = {
    headers: {
      "Content-Type": "application/json",
    },
  };


  async function openModal(id) {
    setTicketId(id);
    setShowDefault(true);
  }
  const [showDefault, setShowDefault] = useState(false);
  const [showDefault1, setShowDefault1] = useState(true);

  async function changerEtat(){
    const { data: response } = await axios.put(
      "http://localhost:3000/api/v1/profile/changestatus/"+ticketId,
      formData2,
      config
    );
    setShowDefault1(false);
    setShowDefault(false);
    setShow(false);
    Swal.fire({
      title: "Success",
      text: "Ticket has been updated",
      icon: "success",
      confirmButtonText: "Cool",
    }).then((result) => {
      if (result.value) {
        history.push("/allTickets");
      }
    })

  }

  const handleClose = () => setShowDefault(false);
  return (
    <>
      <Modal
        className="d-flex justify-content-center"
        show={showDefault}
        onHide={handleClose}
      >
        <Modal.Header>
          <Modal.Title className="h6">Etat ticket</Modal.Title>
          <Button variant="close" aria-label="Close" onClick={handleClose} />
        </Modal.Header>

        <Form onSubmit={(e) => changerEtat()}>
          <Modal.Body>
            <div className="flex justify-content-center">
              <h4>Changer l'etat de ticket </h4>
              <br></br>
              {/*select option*/}
              <Form.Control
                as="select"
                required
                onChange={(e) =>
                  setFormData2({ ...formData2, status: e.target.value })
                }
              >
                <option value="open">En attente</option>
                <option value="progress">En cours</option>
                <option value="closed">Terminé</option>
              </Form.Control>
            </div>
          </Modal.Body>
          <Modal.Footer>
            <Button variant="primary" type="submit">
              Approuver
            </Button>
            <Button
              variant="secondary"
              className="text-gray ms-auto"
              onClick={handleClose}
            >
              Fermer
            </Button>
          </Modal.Footer>
        </Form>
      </Modal>

      <div className="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center py-4">
        <div className="d-block mb-4 mb-md-0">
          <Breadcrumb
            className="d-none d-md-inline-block"
            listProps={{
              className: "breadcrumb-dark breadcrumb-transparent",
            }}
          ></Breadcrumb>
          <h4>Vos tickets support</h4>
        </div>
      </div>
      <div className="d-block mb-4 mb-md-2">
        <Col xs={8} md={6} lg={3} xl={4}>
          <InputGroup>
            <InputGroup.Text>
              <FontAwesomeIcon icon={faSearch} />
            </InputGroup.Text>
            <Form.Control type="text" placeholder="Search" />
          </InputGroup>
        </Col>
      </div>
      <Card border="light" className="table-wrapper shadow-sm">
        <Card.Body className="pt-0">
          <Table className="table-centered table-nowrap rounded mb-0">
            <thead className="thead-light">
              <tr>
                <th className="border-0">#</th>
                <th className="border-0">Sujet</th>
                <th className="border-0">Sender</th>
                <th className="border-0">Etat </th>
                <th className="border-0">Date de réalisation</th>
                <th className="border-0">Actions</th>
              </tr>
            </thead>
            <tbody>
              {data?.map((dm, index) => (
                <tr>
                  <td>{index + 1}</td>
                  <td className="fw-bold">{dm?.subject}</td>
                  <td className="fw-bold">
                    {dm?.prop?.name} {dm?.prop?.lastn}
                  </td>

                  <td>
                    <>
                      {dm?.status === "open" ? (
                        <Badge bg="primary" className="me-1">
                          Open
                        </Badge>
                      ) : dm?.status === "progress" ? (
                        <Badge bg="success" className="me-1">
                          On progress
                        </Badge>
                      ) : dm?.status === "closed" ? (
                        <Badge bg="danger" className="me-1">
                          closed
                        </Badge>
                      ) : (
                        "none"
                      )}
                    </>
                  </td>
                  <td>{moment(dm?.createdAt).format("DD/MM/YYYY HH:mm")}</td>
                  <td>
                    {" "}
                    <CDropdown className="dropleft" direction="dropstart">
                      <CDropdownToggle color="transparant">
                        <span className="icon icon-sm">
                          <FontAwesomeIcon
                            icon={faEllipsisH}
                            className="icon-dark"
                          />
                        </span>
                      </CDropdownToggle>
                      <CDropdownMenu style={{ left: "50px;" }}>
                        <CDropdownItem
                          onClick={() =>
                            history.push("/ticketdetail/" + dm._id)
                          }
                        >
                          <FontAwesomeIcon icon={faEye} className="me-2" />{" "}
                          Details
                        </CDropdownItem>

                        <CDropdownItem onClick={() => openModal(dm._id)}>
                          <FontAwesomeIcon icon={faEdit} className="me-2" />{" "}
                          Etat{" "}
                        </CDropdownItem>
                      </CDropdownMenu>
                    </CDropdown>
                  </td>
                </tr>
              ))}
            </tbody>
          </Table>
          <Card.Footer className="px-3 border-0 d-lg-flex align-items-center justify-content-between">
            <Nav>
              <Pagination className="mb-2 mb-lg-0">
                <Pagination.Prev>Previous</Pagination.Prev>
                <Pagination.Item active>1</Pagination.Item>
                <Pagination.Item>2</Pagination.Item>
                <Pagination.Item>3</Pagination.Item>
                <Pagination.Item>4</Pagination.Item>
                <Pagination.Item>5</Pagination.Item>
                <Pagination.Next>Next</Pagination.Next>
              </Pagination>
            </Nav>
            <small className="fw-bold">
              Showing <b>{2}</b> out of <b>25</b> entries
            </small>
          </Card.Footer>
        </Card.Body>
      </Card>
    </>
  );
}
